* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: aliceblue;
}

.App {
  text-align: center;
}

.header {
  margin-bottom: 0.5em;
  font-family: cursive;
}

.appTask {
  padding: 2em;
  background-color: mediumturquoise;
}

.taskInput {
  padding: 0.5em;
  width: 300px;
}

.taskBtn {
  background-color: coral;
  padding: 0.5em;
  border: 1px solid gray;
  font-weight: 500;
  cursor: pointer;
}

.taskContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: beige;
  width: 50%;
  margin: 15px auto;
  padding: 10px 20px;
}

.taskName {
  font-size: 1.8rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.updateBtn {
  background-color: mediumseagreen;
  padding: 8px;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  margin-right: 10px;
}

.deleteBtn {
  background-color: steelblue;
  padding: 9px;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
}